<template>
  <div class="profile-container">
    <div class="top-nav">
    个人资料
  </div>
  <div class="items">
    <div class="item" @click="handleChange">
      <div class="category">
        昵称
      </div>
      <div class="value">
      {{nickname}}
        <img src="../assets/arrow2.svg" alt="">
      </div>
    </div>
    <div class="item">
      <p>选择您的性别</p>
      <div class="sex">
      <div class="option" @click="handleSex(1)">
        <img v-show="sex=== 1" src="../assets/selected.svg" alt="">
        <img v-show="sex=== 2" src="../assets/unselected.svg" alt="">
        男
      </div>
      <div class="option" @click="handleSex(2)">
        <img v-show="sex===2" src="../assets/selected.svg" alt="">
        <img v-show="sex===1" src="../assets/unselected.svg" alt="">
        女
      </div>
    </div>
    </div>
  </div>
  <div class="button-container">
    <button class="next" @click="handleSubmit">保存</button>
  </div>

  </div>
</template>
<script>
import { MessageBox } from 'mint-ui';

import { mapState } from 'vuex';
import api from '../api/user';

export default {
  data() {
    return {
      nickname: '',
      sex: 1,
    };
  },
  mounted() {
    this.ini();
  },
  computed: {
    ...mapState({
      user: state => state.global.user,
    }),
  },
  methods: {
    ini() {
      this.sex = this.user.sex;
      this.nickname = this.user.nickname;
    },
    handleSex(sex) {
      this.sex = sex;
    },
    async handleSubmit() {
      const user = {
        nickname: this.nickname,
        sex: this.sex,
      };
      const res = await api.edit(user);
      if (res.code === 0) {
        this.$store.commit('setUserNameAndSex', user);
        MessageBox.alert('修改成功');
      }
    },
    handleChange() {
      MessageBox.prompt('修改昵称', '请输入新昵称').then(({ value, action }) => {
        if (value && action === 'confirm') {
          this.nickname = value;
        }
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.sex {
  width: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.option {
  width: 50%;
  display: flex;
  align-items: center;
  img {
    width: 30px;
    height: 30px;
    margin-right: 5px;
  }
}
.items {
  width: 100%;
  .item {
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
    border-bottom: 2px solid #f2f2f2;
    .value > img {
      width: 15px;
      height: 15px;
    }
  }
}
.button-container {
  display: flex;
  justify-content: center;
}
.next {
  width: 250px;
  background: #fdda06;
  border: 0;
  border: 10px;
  height: 45px;
  margin-top: 100px;
  color: #666;
}
.next[disabled] {
  color: white;
  background: #f2f2f2;
}
</style>





