<template>
<div class="gray">
  <div class="user-title-container" :style="{height: height + 'px'}">
    <div class="top">
      <div class="like" @click="handleFollow(user.id, 'user', user.isFollow)">

        <img v-if="user.isFollow" src="../assets/liked.svg" alt="">
        <img v-else src="../assets/like.svg" alt="">
        <p v-if="user.isFollow" class="followit">已关注</p>
        <p v-else>关注</p>
      </div>
    </div>
    <div class="user">
      <img :src="user.headimgurl" alt="">
      {{user.nickname}}
    </div>
    <div class="buttons">
      <div class="follow" @click="goto('Follower-list')">
        关注: {{user.follow_number}}
      </div>
      <div class="follower" @click="goto('Fans-list')">
        粉丝: {{user.fans_number}}
      </div>
    </div>
  </div>
    <div class="tabs">
      <div :class="['item', {'item-active': nav === 'looking'}]" @click="setNav('looking')">{{sex}}的动态</div>
      <div :class="['item', {'item-active': nav === 'activity'}]" @click="setNav('activity')">{{sex}}参与过</div>
      <div :class="['item', {'item-active': nav === 'comments'}]" @click="setNav('comments')">{{sex}}的评论</div>
    </div>
    
    <div class="tab">
      <div class="tab-container" v-if="nav === 'looking'" >
        <p v-show="looking.length < 1">暂时没有动态</p>
        
        <ul v-infinite-scroll="lookingMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10" key="looking">
          <li v-for="(item, i) in looking" :key="i">
            <p class="message">报名了活动<span style="color: #369;">{{item.name}}</span></p>
            <card :item="item">
              </card>
          </li>
        </ul>
      </div>
      <div class="tab-container" v-else-if="nav === 'activity'" >
        <p v-show="activity.length < 1">暂时没有参与</p>
        <ul v-infinite-scroll="activityMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10" key="activity">
          <li v-for="(item, i) in activity" :key="i">
            <p class="message">参与了活动<span style="color: #369;">{{item.name}}</span></p>
            <card :item="item">
              </card>
          </li>
        </ul>
      </div>
      <div class="tab-container comment-container" v-else-if="nav === 'comments'" >
        <p v-show="comments.length < 1">暂时没有评论</p>
          <ul v-infinite-scroll="commentMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10" key="comments">
            <li v-for="(item, i) in comments" :key="i">
              <comment-item :item="item" :to="to">

              </comment-item>
            </li>
          </ul>
      </div>
    </div>

</div>
</template>
<script>
import { mapState } from 'vuex';
import moment from 'moment';
import Activity from '../components/Activity';
import api from '../api/user';
import api2 from '../api/activity';
import commentApi from '../api/comments';
import Card from '../components/card';
import CommentItem from '../components/Comment-item';

const parseNumType = num =>
  num === 1 ? 'activity' : num === 2 ? 'store' : 'information';

export default {
  components: { Card, Activity, CommentItem },
  mounted() {
    const rate = window.innerWidth / 16;
    this.height = rate * 9.5;
    const id = this.$route.params.id;
    this.id = id;
    this.handleIn();
  },
  data() {
    return {
      user: {},
      sex: '',
      height: '',
      looking: [],
      page: {
        comments: 1,
        activity: 1,
        looking: 1,
      },
      comments: [],
      activity: [],
    };
  },
  computed: {
    ...mapState({
      nav: state => state.user.nav,
    }),
  },
  watch: {
    nav(v, o) {
      if (v !== 0 && v === 'comments') {
        this.handleComment();
      }
    },
  },
  methods: {
    async handleIn() {
      const id = this.$route.params.id;
      this.id = id;
      const res = await api.get(id);
      this.user = res.data;
      this.sex = res.data.sex === 1 ? '他' : '她';
    },
    async handleLooking() {
      const res = await api2.getMoment(this.id)(this.page.looking);
      this.page.looking += 1;

      /**
       * id,
       * name,
       * location,
       * link
       */
      const looking = res.data.map(v => ({
        ...v,
        name: v.title,
        id: v.id,
        location: v.address,
        link: 'Activity-detail',
        time: moment(v.start_time * 1000).format('YYYY-MM-DD'),
      }));
      this.looking.push(...looking);
    },
    async handleActivity() {
      const res = await api2.getOtherList(this.id)(this.page.activity);
      this.page.activity += 1;

      const activity = res.data.map(v => ({
        ...v,
        name: v.title,
        id: v.id,
        location: v.address,
        link: 'Activity-detail',
        time: moment(v.start_time * 1000).format('YYYY-MM-DD'),
      }));
      this.activity.push(...activity);
    },
    async handleComment() {
      const res = await commentApi.getOther(this.id)(this.page.comments);
      this.page.comments += 1;
      this.comments.push(
        ...res.data.map(v => ({
          ...v,
          time: moment(v.create_time * 1000).format('YYYY-MM-DD'),
          sourceTime: moment(v.relation_time * 1000).format('YYYY-MM-DD'),
        })),
      );
    },
    commentMore() {
      this.handleComment();
    },
    activityMore() {
      this.handleActivity();
    },
    lookingMore() {
      this.handleLooking();
    },
    handleFollow(id, type, follow) {
      const cb = () => this.handleIn();
      if (follow) {
        this.$store.dispatch('unfollow', { id, type, cb });
      } else {
        this.$store.dispatch('follow', { id, type, cb });
      }
    },
    setNav(nav) {
      this.$store.commit('setUserNav', nav);
    },
    loadMore() {},
    goto(name) {
      this.$router.push({
        name,
        params: { id: this.id },
      });
    },
    to(id, numType) {
      const type = parseNumType(numType);

      if (type === 'activity') {
        this.$router.push({
          name: 'Activity-detail',
          params: {
            id,
          },
        });
      } else if (type === 'store') {
        this.$router.push({
          name: 'Store-detail',
          params: {
            id,
          },
        });
      } else if (type === 'information') {
        this.$router.push({
          name: 'Information-detail',
          params: {
            id,
          },
        });
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.user-title-container {
  width: 100%;
  background: url('../assets/user-title.png');
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  .top {
    top: 0;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    img {
      width: 18px;
      margin-bottom: 2px;
    }
    .like {
      color: white;
      text-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
      margin: 10px 8px 0 0;
      p {
        display: inline;
        color: #ff5d57;
      }
      .followit {
        color: white;
      }
    }
  }
  .user {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    img {
      width: 25%;
      border-radius: 50%;
    }
    color: white;
    font-size: 22px;
    font-weight: bold;
  }
  .buttons {
    display: flex;
    padding: 5px 25%;
    justify-content: space-between;

    .follow,
    .follower {
      font-size: 13px;
      padding: 3px 10px;
      border: 2px solid white;
      border-radius: 20px;
      color: white;
    }
    .follower {
      background: #ff5d57;
      border-color: #ff5d57;
    }
  }
}
.tabs {
  width: 100%;
  display: flex;
  padding: 5px 45px;
  justify-content: space-between;
  background: white;

  .item {
    color: #999;
  }
  .item-active {
    color: #333;
    border-bottom: 2px solid #fdda06;
  }
}
.tab {
  width: 100%;
  height: 100%;

  .tab-container {
    display: block;
    width: 100%;
    background: #f2f2f2;
    padding: 15px;
    min-height: calc(100% + 10px);
    > p {
      text-align: center;
    }
    li {
      margin-bottom: 10px;
    }
    .message {
      margin: 0;
      font-size: 13px;
    }
  }
  .comment-container {
    padding: 15px 0;
  }
}
.gray {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #f2f2f2;
}
</style>





