import request from '../utils/request';

const login = data =>
  request({
    url: '/store/login/login',
    method: 'post',
    data,
  });
const getInfo = () =>
  request({
    url: '/store/store/detail',
    method: 'get',
  });
export default {
  login,
  getInfo,
};



